import React from "react";
import { Container, Row, Col } from 'react-bootstrap';
import Spinner from './Spinner.js';
import DownloadMaterials from './DownloadMaterials.js';

class Detail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  componentDidMount() {
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");
    window.scrollTo(0, 0);
  }

  render() {
    if(this.props.dataReady){
      var i;
      var data;
      var eventTitle;
      const events = this.props.events;
      data = this.props.data.find(item => item.title === this.props.match.params.detail);
      if(!data){
        window.location.href = "/";
      }
      const eventItem = eventTitle = events.find(event => event.id === data.event_id);
      eventTitle = eventItem ? eventItem.title : '';
      return (
        <div className="detailWp">
          <Container>
            <Row>
              <Col xs={12} xl={6} className={data.cover_img ? '' : 'd-none'}>
                <CoverImg data={data} />
              </Col>
              <Col xs={12} xl={data.cover_img ? 6 : 12}>
                <h2>{data.title}</h2>
                <h5><a href={"#/events/"+eventTitle}>{eventTitle}</a></h5>
                <div className="content">
                {data.content.split('\n').map((i, j) => {
                    return <p key={j}>{i}</p>
                })}
                </div>
                <div className="share">
                  <ul className="nav">
                    <li><a href={"https://www.facebook.com/sharer.php?u="+window.location.href} target="_blank" rel="noopener noreferrer"><i className="fa fa-facebook"></i></a></li>
                    <li><a href={"https://twitter.com/share?url="+window.location.href} target="_blank" rel="noopener noreferrer"><i className="fa fa-twitter"></i></a></li>
                    <li><a href={"https://wa.me/?text="+window.location.href} target="_blank" rel="noopener noreferrer"><i className="fa fa-whatsapp"></i></a></li>
                  </ul>
                </div>
              </Col>
              <Col xs={12}>
                <Sources data={data} />
                <Related items={this.props.data} data={data} />
                <Download dataReady={this.props.dataReady} events={this.props.events} filter={eventTitle} event={eventItem} />
              </Col>
            </Row>
          </Container>
        </div>
      );
    }
    return (
      <div className="text-center">
        <br />
        <Spinner />
        <br />
      </div>
    )
  }
}

function Download(props) {
  if(props.event && props.event.download_images){
    return (
      <div className="downloadWp">
        <h3>Download</h3>
        <DownloadMaterials events={props.events} dataReady={props.dataReady} filter={props.filter} />
      </div>
    )
  }
  return "";
}

function Related(props) {
  const prev = props.items[props.items.indexOf(props.data) - 1];
  const next = props.items[props.items.indexOf(props.data) + 1];
  if(prev || next){
    return (
      <div className="related">
        <Row>
          <Col xs={6} className="prev">
            <PrevNext data={prev} />
          </Col>
          <Col xs={6} className="next">
            <PrevNext data={next} />
          </Col>
        </Row>
      </div>
    )
  }
  return "";
}

function PrevNext(props) {
  if(props.data){
    return (
      <a href={"#/d/"+props.data.title}>
        {props.data.title}
      </a>
    )
  }
  return "";
}

function Sources(props) {
  if(props.data.sources){
    return (
      <div className="foot-note">
        <h4>Sources</h4>
        {
          props.data.sources.split('\n').map((i, j) => {
              return <div key={j}><a href={i} target="_blank" rel="noopener noreferrer"><i>{i}</i></a></div>
          })
        }
      </div>
    )
  }
  return "";
}

function CoverImg(props) {
  if(props.data.cover_img){
    return (
      <img src={props.data.cover_img} alt={props.data.title} className="w-100 border shadow coverImg" />
    )
  }
  return "";
}

export default Detail;
