import React from "react";
import Spinner from './Spinner.js';
import { Row, Col } from 'react-bootstrap';


class DownloadMaterials extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }

  componentDidMount() {
  }

  render() {
    if (this.props.hidden) {
      return "";
    }
    if (this.props.dataReady) {
      return (
        <div className="downloadWp">
          {
            this.props.events.map((obj, j) => {
              return <EventRow key={j} obj={obj} filter={this.props.filter} />
            })
          }
        </div>
      )
    }
    return (
      <div className="text-center">
        <br />
        <Spinner />
        <br />
      </div>
    )
  }
}

class EventRow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      index: 0,
      open: false,
    };
  }

  handleClickPrev = () => {
    this.setState({ index: this.state.index - 1 });
  };

  handleClickNext = () => {
    this.setState({ index: this.state.index + 1 });
  };

  handleOpen = () => {
    this.setState({ open: true });
  }

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {
    if(this.props.obj.download_images && (!this.props.filter || this.props.filter === this.props.obj.title)){
      function Title(props){
        if(!props.filter){
          return (
            <Col xs={12}>
              <h3>{props.obj.title}</h3>
            </Col>
          )
        }
        return <Col xl={12} className="mt-5"></Col>;
      }
      return (
        <Row>
          <Title filter={this.props.filter} obj={this.props.obj} />
          {
            this.props.obj.download_images.split('\n').map((img, j) => {
              return (
                <Col xs={6} xl={4} key={j}>
                  <a href={img} target="_blank" rel="noopener noreferrer" download><img src={img} alt="" /></a>
                </Col>
              )
            })
          }
        </Row>
      )
    }
    return "";
  }
}

export default DownloadMaterials;
