import React from "react";
import { Container } from 'react-bootstrap';
import DownloadMaterials from './DownloadMaterials.js';

class Download extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  componentDidMount() {
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div className="aboutWp">
        <Container>
          <h2>Download</h2>
          <DownloadMaterials events={this.props.events} dataReady={this.props.dataReady} />
        </Container>
      </div>
    );
  }
}

export default Download;
