import React from "react";
import Tabletop from 'tabletop';
import { Container } from 'react-bootstrap';
import Spinner from './Spinner.js';

class About extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      dataReady: false
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");

    Tabletop.init({
      key: '1MIhGQz7OErQ5rP-DU4UpP0X2hJBGpNZ2r2ZKAEMZSsA',
      callback: googleData => {
        this.setState({
          data: googleData,
          dataReady: true
        });
      },
      simpleSheet: true
    });
  }

  render() {
    return (
      <div className="aboutWp">
        <Container>
          <h2>About</h2>
          <Content ready={this.state.dataReady} data={this.state.data} />
        </Container>
      </div>
    );
  }
}

function Content(props) {
  if (props.ready) {
    return (
      <div>
        {
          props.data[0].content.split('\n').map((i, j) => {
            return <p key={j}>{i}</p>
          })
        }
      </div>
    )
  }
  return (
    <div className="text-center">
      <br />
      <Spinner />
        <br />
    </div>
  )
}

export default About;
