import React from "react";
import Tabletop from 'tabletop';
import { Route } from "react-router-dom";
import { HashRouter } from 'react-router-dom';
import Header from "./Header.js";
import Footer from "./Footer.js";
import Home from "./Home.js";
import About from "./About.js";
import Download from "./Download.js";
import Detail from "./Detail.js";

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      scrollDown: 0,
      data: [],
      events: [],
      menuOpen: false,
      finishEvents: false,
      finishData: false
    }
    this.handleScroll = this.handleScroll.bind(this);
    this.handleFinishEvents = this.handleFinishEvents.bind(this);
    this.handleFinishData = this.handleFinishData.bind(this);
  }

  handleFinishEvents = () => {
    this.setState({
      finishEvents: true
    });
  }

  handleFinishData = () => {
    this.setState({
      finishData: true
    });
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);

    Tabletop.init({
      key: '1QClBiSbsZKGlzugA1_CUKE8uI9-0PNDb9vkCmrEka3M',
      callback: googleData => {
        this.setState({
          events: googleData
        });
        this.handleFinishEvents();
      },
      simpleSheet: true
    });

    Tabletop.init({
      key: '1CNnSjBUnSRoT_dW5aJwKmzbWqf3_80PhM9RNBMYfgM8',
      orderby: 'date',
      callback: googleData => {
        this.setState({
          data: googleData
        });
        this.handleFinishData();
      },
      simpleSheet: true
    });
  }

  handleScroll = () => {
    let supportPageOffset = window.pageXOffset !== undefined;
    let isCSS1Compat = ((document.compatMode || '') === 'CSS1Compat');
    let scroll = {
       x: supportPageOffset ? window.pageXOffset : isCSS1Compat ? document.documentElement.scrollLeft : document.body.scrollLeft,
       y: supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop
    };
    this.setState({
      scrollDown: scroll.y
    });
  }

  openMenu = () => {
    this.setState({
      menuOpen: true
    });
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");
  }

  closeMenu = () => {
    this.setState({
      menuOpen: false
    });
    document.getElementsByClassName("submenu-tg")[0].classList.remove("open");
  }

  render() {
    return (
      <HashRouter>
        <div className={"wrapper" + (this.state.menuOpen ? ' menu-open' : '') + (this.state.scrollDown > 20 ? ' scroll-down' : '') + (this.state.scrollDown >= window.document.body.offsetHeight - window.innerHeight ? ' reach-bottom' : '')}>
          <Header events={this.state.events} openMenu={this.openMenu} closeMenu={this.closeMenu} />

          <main>
            <Route exact path="/" render={(props) => <Home {...props} current="home" data={this.state.data} events={this.state.events} dataReady={this.state.finishEvents && this.state.finishData} />} />
            <Route exact path="/about" render={(props) => <About {...props} />} />
            <Route path="/events/:event" render={(props) => <Home {...props} data={this.state.data} events={this.state.events} dataReady={this.state.finishEvents && this.state.finishData} />} />
            <Route exact path="/d/:detail" render={(props) => <Detail {...props} data={this.state.data} events={this.state.events} dataReady={this.state.finishEvents && this.state.finishData} />} />
            <Route exact path="/download" render={(props) => <Download {...props} events={this.state.events} dataReady={this.state.finishEvents} />} />
          </main>

          <Footer />
        </div>
      </HashRouter>
    )
  }
}
